import * as Yup from "yup";

const ContactUs = Yup.object({
  fullName: Yup.string("    ")
    .max(50, "  50   ")
    .required("         "),
  email: Yup.string()
    .email("      ")
    .required("      "),
  phoneNumber: Yup.string("    ").max(
    15,
    " 15 "
  ),
  comment: Yup.string("    ")
    .max(1500, " 1500   ")
    .required("     "),
});

export { ContactUs };
