export function salary(type) {
  switch (type) {
    case 1:
      return " 1  ";

    case 2:
      return "1  2.5  ";

    case 3:
      return "2.5  3.5  ";

    case 4:
      return "3.5  5  ";

    case 5:
      return "5  8  ";

    case 6:
      return " 8  ";

    case 7:
      return " ";

    case 8:
      return " ";

    default:
       return " ";
  }
}
