import * as Yup from "yup";

const JobExprience = Yup.object({
  workTitle: Yup.string("    ")
    .max(50, " 50   ")
    .required("     "),
  companyName: Yup.string("    ")
    .max(50, " 50   ")
    .required("     "),
  startDate: Yup.string("    ").max(
    300,
    " 50   "
  ),
  endDate: Yup.string("    ").max(
    300,
    " 50   "
  ),
  description: Yup.string("    ").max(
    50,
    " 500   "
  ),
});

export { JobExprience };
