import * as Yup from "yup";
const faRegex = /^[ \s]+$/;
const enRegex = /^[ A-Za-z]+$/;
const numRegex = /^-?[0-9][0-9,\.]+$/;

const CompleteRegister = Yup.object({
  ManagmentFullName: Yup.string("    ").required(
    "         "
  ),
  PersianFullName: Yup.string()
    .matches(faRegex, "    ")
    .required("      "),
  EngFullName: Yup.string()
    .matches(enRegex, "    ")
    .required("      "),
  EmergencPhone: Yup.string()
    .matches(numRegex, "   ")
    .min(5, " 5 ")
    .max(20, " 20 ")
    .required("      "),
  url: Yup.string().max(40, " 40 "),
  Email: Yup.string()
    .email("      ")
    .required("      "),
  NumberOfStaff: Yup.number()
    .min(0, "     ")
    .required("     "),
});

export { CompleteRegister };
