import * as Yup from "yup";

const CreateAdValidate = Yup.object({
  title: Yup.string().required("     "),
  // staticNumber: Yup.string().required("      "),
  phoneNumber: Yup.string().required("     "),
  email: Yup.string(),
  // workTime: Yup.string().required("     "),
  fieldOfActivity: Yup.string().required("      "),
  city: Yup.string().required("     "),
  typeOfCooperation: Yup.number()
    .min(1, "     ")
    .required("     "),
  salary: Yup.number()
    .min(1, "     ")
    .required("     "),
  gender: Yup.number()
    .min(1, "    ")
    .required("    "),
  workExperience: Yup.number()
    .min(1, "      ")
    .required("      "),
  degreeOfEducation: Yup.number()
    .min(1, "     ")
    .required("     "),
  descriptionOfJob: Yup.string().required("     "),
  military: Yup.string(),
  address: Yup.string().required("     "),



});

export { CreateAdValidate };
